import React, { useEffect } from 'react';
import { Provider, useDispatch } from 'react-redux';
import { BrowserRouter, Route, Routes } from 'react-router-dom';
import _ from 'lodash';
import { RouteTypes } from '../constants/Routes/RouteTypes';
import FloorPlan from '../screens/FloorPlan';
import Apartment from '../screens/Apartment';
import Pricing from '../screens/Pricing';
import Item from '../screens/Item';
import { store } from '../redux/store';
import { useSelector } from 'react-redux';
import { setConfiguration } from '../redux/slices/configurationSlice';

function AppWrapper() {
  const configuration = useSelector((state) => { return state.configuration; });

  const renderScreen = () => {
    console.log('configuration');
    console.log(configuration);
      switch (configuration.screen) {
        case 'floorplans':
            return <FloorPlan configuration={configuration} />;
        case 'apartments':
          return <Apartment />;
        case 'lease terms':
          return <Pricing />;
        default:
          return <FloorPlan configuration={configuration} />;
      }
    }

  useEffect(() => {
    renderScreen();
  }, [configuration]);

  return (
    renderScreen()
  );
}

export default AppWrapper;
